<template>
  <div class="card">
    <div class="card-header border-0">
      <div class="row align-items-center">
        <div class="col">
          <h3 class="mb-0">Tables</h3>
        </div>
        <div class="col text-right">
          <base-button size="sm" type="primary" @click="toggleTableModal">Add Table</base-button>
        </div>
      </div>
    </div>

    <div class="table-responsive">
      <base-table thead-classes="thead-light" :data="tableData">
        <template slot="columns">
          <th>Name</th>
          <th>Fields</th>
          <th>Action</th>
        </template>

        <template slot-scope="{row}">
          <th scope="row">{{row.name}}</th>
          <td>{{row.fields.length}}</td>
          <td>
            <base-button size="sm" type="primary" iconOnly icon="ni ni-ruler-pencil" title="Edit"></base-button>
          </td>
        </template>
      </base-table>
    </div>
    <modal :show="showModal" @close="toggleTableModal" type="large">
      <div slot="header">Add Table</div>
      <div>
        <form role="form" @submit.prevent="login">
          <base-input
            class="input-group-alternative mb-3"
            placeholder="Table Name"
            :error="errors.tableName"
            v-model="active_table.name"
          ></base-input>
          <base-textarea
            class="input-group-alternative mb-4"
            placeholder="Table Description"
            v-model="active_table.description"
          ></base-textarea>
          <base-table thead-classes="thead-light" :data="active_table.fields">
            <template slot="columns">
              <th>Name</th>
              <th>Type</th>
              <th>Options</th>
              <th>Action</th>
            </template>

            <template slot-scope="{row}">
              <td>
                <base-input class="input-group-sm mb-0" placeholder="Name" v-model="row.name"></base-input>
              </td>
              <td>
                <base-select
                  class="input-group-sm mb-0"
                  v-model="row.field_type"
                  :value="row.field_type"
                  :options="field_types"
                ></base-select>
              </td>
              <td>
                <base-input
                  class="input-group-sm mb-0"
                  v-show="row.field_type == 3"
                  placeholder="Comma separated options"
                  v-model="row.field_options"
                ></base-input>
              </td>
              <td>
                <base-button
                  size="sm"
                  type="danger"
                  iconOnly
                  icon="ni ni-fat-remove"
                  title="Remove"
                  @click="removeField(row.name)"
                ></base-button>
              </td>
            </template>
            <tr slot="custom-row">
              <td>
                <base-input class="input-group-sm mb-0" placeholder="Name" v-model="new_field.name"></base-input>
              </td>
              <td>
                <base-select
                  class="input-group-sm mb-0"
                  v-model="new_field.field_type"
                  :options="field_types"
                ></base-select>
              </td>
              <td>
                <base-input
                  class="input-group-sm mb-0"
                  v-show="new_field.field_type == 3"
                  placeholder="Comma separated options"
                  :value="new_field.field_options"
                  v-model="new_field.field_options"
                ></base-input>
              </td>
              <td>
                <base-button
                  size="sm"
                  type="primary"
                  iconOnly
                  icon="ni ni-fat-add"
                  title="Add"
                  @click="addField"
                ></base-button>
              </td>
            </tr>
          </base-table>
          <div class="text-center"></div>
        </form>
      </div>
      <div slot="footer">
        <base-button type="primary" class="sm" @click="addTable">Save</base-button>
      </div>
    </modal>
  </div>
</template>
<script>
export default {
  name: "data-tables-table",
  data() {
    return {
      errors: {
        tableName: null
      },
      tableData: [],
      active_table: {
        fields: []
      },
      new_field: {
        name: "",
        field_type: 0,
        field_options: null
      },
      field_types: [
        { value: 0, name: "Text" },
        { value: 1, name: "Number" },
        { value: 2, name: "Date" },
        { value: 3, name: "enum" }
      ],
      showModal: false
    };
  },
  mounted() {
    var dt = this;
    this.$http.get("/api/data-tables").then(response => {
      for (var d in response.data) {
        dt.tableData.push({
          id: response.data[d].id,
          name: response.data[d].name,
          fields: response.data[d].data_fields
        });
      }
    });
  },
  methods: {
    toggleTableModal: function(toggle = !this.showModal) {
      this.showModal = toggle;
    },
    addField: function() {
      this.new_field.field_type = parseInt(this.new_field.field_type);
      console.log(this.new_field);
      this.active_table.fields.push(this.new_field);
      this.new_field = { name: "", field_type: 0, field_options: null };
    },
    removeField: function(name) {
      for (let i = 0; i < this.active_table.fields.length; i++) {
        if (this.active_table.fields[i].name == name) {
          this.active_table.fields.splice(i, 1);
        }
      }
    },
    addTable: function() {
      if (!this.active_table.name) {
        this.errors.tableName = "The table name is required";
      }
      //Save Table data
    }
  }
};
</script>
